<template>
  <div class="w-full px-4 py-2 border-b hover:bg-lightest flex flex-row">
    <div class="flex-none">
      <a @click="pushToProfilePage(user.id)">
        <img
          :src="`${user.avatar || 'default_profile.png'}`"
          class="h-12 w-12 rounded-full"
         />
      </a>
    </div>
    <div class="ml-2 flex flex-col w-full">
      <div class="flex flex-row justify-between w-full">
        <div class="flex flex-col">
          <p class="font-bold">{{ user.username }}</p>
          <p class="text-dark text-sm">@{{ user.id }}</p>
        </div>
        <div v-if="profile && profile.id !== user.id">
          <button
            v-if="!user.following"
            @click="follow"
            class="ml-auto text-blue font-bold px-4 py-1 rounded-full border border-blue mb-2 hover:bg-lightblue"
          >
            Follow
          </button>
          <button
            v-if="user.following"
            @mouseover="followingLabel = 'Unfollow'"
            @mouseleave="followingLabel = 'Following'"
            @click="unfollow"
            class="ml-auto text-white bg-blue font-bold px-4 py-1 rounded-full border mb-2 hover:bg-red-700"
          >
            {{ followingLabel }}
          </button>
        </div>
      </div>
      <div>
        <p>{{ user.bio }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import {reactiveService} from "@/service/service";

export default {
  name: "User",
  props: {
    user: {
      id: "",
      username: "",
      type: Object,
      required: true,
      avatar: "",
      following: false,
    }
  },
  data() {
    return {
      followingLabel: "Following",
      profile: {},
    };
  },
  methods: {
    async follow() {
      try {
        await reactiveService.followUser(this.user.id);
        this.user.following = true;
      } catch (err) {
        console.error(`failed to follow [${this.user.id}]`, err);
      }
    },
    async unfollow() {
      try {
        await reactiveService.unfollowUser(this.user.id);
        this.user.following = false;
      } catch (err) {
        console.error(`failed to unfollow [${this.user.id}]`, err);
      }
    },
    async pushToProfilePage(profileId) {
      this.$router.push({
        name: "Profile",
        params: {
          id: profileId
        }
      });
    },
  },
  async created() {
    console.log("loading component:", this.$options.name);
    this.profile = reactiveService.getOwnerProfile();
    this.profile.background_image = await reactiveService.getImage({userId:this.profile.id, key:this.profile.background_image_key});
    this.profile.avatar = await reactiveService.getImage({userId:this.profile.id, key:this.profile.avatar_key})
    this.user.avatar = await reactiveService.getImage({userId:this.user.id, key:this.user.avatar_key})
  },
};
</script>
